<template>
  <div class="write">
    <my-header :child="type"></my-header>
    <div class="write-content">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" >
        <el-form-item label="文章标题" prop="title">
          <el-input v-model="ruleForm.title" placeholder="请输入文章标题"></el-input>
        </el-form-item>

        <el-form-item label="文章副标题" prop="smalltitle">
          <el-input v-model="ruleForm.smalltitle" placeholder="请输入文章副标题"></el-input>
        </el-form-item>

        <el-form-item label="文章类型" prop="type">
          <el-select v-model="ruleForm.type" placeholder="请选择文章类型">
            <el-option label="java" value="0"></el-option>
            <el-option label="html" value="1"></el-option>
            <el-option label="css" value="2"></el-option>
            <el-option label="js" value="3"></el-option>
          </el-select>
        </el-form-item>


        <el-form-item label="文章配图" prop="desc">
          <el-upload
            class="avatar-uploader"
            action="https://localhost3000/game/uploadImg"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="文章内容" prop="content">
          <el-input type="textarea" v-model="ruleForm.content"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>


<script>
import myHeader from "./../header";
import {postBlog} from './../../services/help.js';
export default {
  components: {
    myHeader
  },
  data() {
    return {
      imageUrl: "",
      type: "login",
      ruleForm: {
        title: "",
        smalltitle: "",
        type: "",
        content:'',
      },
      rules: {
        title: [
          { required: true, message: "请输入文章标题", trigger: "blur" },
          { min: 3, message: "标题最少三个字符", trigger: "blur" }
        ],
        smalltitle: [
          { required: true, message: "请填写小标题", trigger: "blur" }
        ],
        type: [
          { required: true, message: "请选择文章标题", trigger: "blur" }
        ],
        content: [
          { required: true, message: "请填写文章内容", trigger: "blur" }
        ],
      }
    };
  },
  methods: {
    // 表单验证
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let parmas = {
            main_title:this.ruleForm.title,
            small_title:this.ruleForm.smalltitle,
            type:this.ruleForm.type,
            content:this.ruleForm.content,
          };
          postBlog(parmas).then(res=>{
            console.log(res);
          })
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  created(){

  }
};
</script>

<style scoped>
.write-content {
  width: 1000px;
  margin: 50px auto;
  margin-bottom: 40px;
}
.demo-ruleForm {
  width: 80%;
  margin: 0 auto;
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>




