<template>
  <div style="margin-top:80px;">
    <my-header :child="type"></my-header>
    <section class="main">
      <div class="des-top"></div>
       <div class="des-content">
          <el-form :model="ruleForm" style="width:80%;margin:0 auto;" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" >
            <el-form-item label="文章标题" prop="title">
              <el-input v-model="ruleForm.title" placeholder="请输入文章标题"></el-input>
            </el-form-item>

            <el-form-item label="文章副标题" prop="smalltitle">
              <el-input v-model="ruleForm.smalltitle" placeholder="请输入文章副标题"></el-input>
            </el-form-item>

            <el-form-item label="文章类型" prop="type">
              <el-select v-model="ruleForm.type" placeholder="请选择文章类型">
                <el-option label="java" value="0"></el-option>
                <el-option label="html" value="1"></el-option>
                <el-option label="css" value="2"></el-option>
                <el-option label="js" value="3"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="文章内容" prop="content">
              <el-input type="textarea" v-model="ruleForm.content"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
              <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
          </el-form>
       </div>
      <div class="des-bottom"></div>
    </section>
  </div>
</template>

<script>
import Header from "../header";
export default {
  components:{
    myHeader: Header,
  },
  data(){
     return {
      imageUrl: "",
      type: "write",
      ruleForm: {
        title: "",
        smalltitle: "",
        type: "",
        content:'',
      },
      rules: {
        title: [
          { required: true, message: "请输入文章标题", trigger: "blur" },
          { min: 3, message: "标题最少三个字符", trigger: "blur" }
        ],
        smalltitle: [
          { required: true, message: "请填写小标题", trigger: "blur" }
        ],
        type: [
          { required: true, message: "请选择文章标题", trigger: "blur" }
        ],
        content: [
          { required: true, message: "请填写文章内容", trigger: "blur" }
        ],
      }
    };
  },
}
</script>

<style scoped>
.el-form-item{
  margin-top: 20px;
}
</style>





