import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const dailog = {
  state:{
    show:false,
    msg:''
  },
  mutations:{
    showDailog(state,data){
      state.show = true
      state.msg = data
    },
    hideDailog(state){
      state.show = false
    }
  }
}
const validate = {
  state:{
    show:false,
    msg:'',
  },
  mutations:{
    showValidate(state,data){
      console.log('这个组件让他显示了')
      state.show = true
      state.msg =  data
    },
    hideValidate(state){
      console.log('这个隐藏方法执行了')
      state.show = false
    }
  }
}
export default new Vuex.Store({
  modules:{
    dailog:dailog,
    validate:validate
  }
})



